import React from "react";
import "./PortfolioCard.css"

export default function PortfolioCard(props) {
    return (
        <article class="portfolio__card">
            <img src={props.img} alt={props.name} />
            <a class="portfolio__card__details" href={props.URL} target="_blank" rel="noopener noreferrer">
                <p>{props.name}</p>
                <p>{props.langs}</p>
                <p>click to open in a new tab</p>
                <p></p>
            </a>
        </article>
    )
}